"use client";

import "./Modal.css";

export default function ConfirmModal({
  open,
  title,
  description,
  onCancel,
  onConfirm,
}) {
  if (!open) return null;

  return (
    <div className="modalBackdrop">
      <div className="modal">
        <div style={{ textAlign: "center" }}>
          <img
            src="/images/logo.png"
            alt="success"
            style={{ width: 140, marginBottom: 16 }}
          />

          <h2>{title}</h2>

          <p style={{ marginTop: 12, lineHeight: 1.6 }}>{description}</p>
        </div>

        <div className="modalFooter" style={{ justifyContent: "center" }}>
          <button className="outlineBtn" onClick={onCancel}>
            Cancel
          </button>
          <button className="primaryBtn" onClick={onConfirm}>
            Confirm
          </button>
        </div>
      </div>
    </div>
  );
}
